.App-div {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.overview-text {
    /* position: absolute; */
    width: 565px;
    height: 587px;
    border-radius: 29px;
    box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.15);
}

.profil {
    display: flex;
    align-items: center;
    margin: 40px 0 66px 32px;
}

.profil-styling {
    height: 49px;
    margin-right: 31px;
}

.calender-styling {
    height: 49px;
    margin-right: 20px;
}

.my-bookings {
    display: flex;
    align-items: center;
    margin: 0 0 33px 33px;
}

.overview-text p {
    font-style: italic;
    margin: 0 0 242px 0;
}

#no-bookings-text {
    margin: 0 0 25% 0;
}


#booking-button {
    /*     margin: 0 0 68px 0; */
    box-sizing: border-box;
    background-color: #385C7F;
    border: none;
    border-radius: 14px;
    font-size: 32px;
    line-height: 51px;
    font-weight: 700;
    color: white;
    width: 362px;
    height: auto;
    /*     margin-bottom: 5%; */
}

#booking-button:hover {
    background-color: #577FA7;
    cursor: pointer;
}